.PanelButton {
  background-color: #7691e4;
  padding: 12px;
  text-align: left;
  cursor: pointer;
}

.PanelButtonActive {
  background-color: #86a8ff;
  padding: 12px;
  color: rgb(0, 0, 0);
  text-align: left;
  cursor: pointer;
}

.PanelButton:hover {
  background-color: #6184de;
}

.LeftPanel {
  position: relative;
  left: 0;
  top: 0;
  padding-top: 70px;
  overflow: auto;
  background-color: #7691e4;
  width: 200px;
  float: left;
  height: 100%;
}

.settingTitle {
  position: relative;
  text-align: left;
  margin-left: 220px;
}

.SettingArea {
  position: fixed;
  right: 0;
  bottom: 0;
  top: 60px;
  left: 200px;
  text-align: left;
  padding: 12px;
  overflow: auto;
}

.WallpaperContainer{
    position: static;
    width: 100%;
    height: 400px;
    overflow-y: scroll;
    padding: 5px;
    border-radius: 5px;
    background-color: rgb(226, 226, 226);
}

.isOnlinePic{
    position: absolute;
    width: 30px;
    right: 5px;
    bottom:10px;
}
.App {
  font-family: sans-serif;
  text-align: center;
}

.UserAction {
    width: 300px;
    height: 20px;
    margin: 4px;
    padding: 4px;
    color: rgb(0, 0, 0);
}

.UserAction:hover{
    color: rgb(17, 0, 255);
    cursor: pointer;
}

.UserActionlabel {
    float:  left;
    margin-right: 5px;
}

.AvatarsContainer{
    position: static;
    width: 100%;
    height: 400px;
    overflow-y: scroll;
    padding: 5px;
    border-radius: 5px;
    background-color: rgb(226, 226, 226);
}
.App {
  position: fixed;
  font-family: sans-serif;
  text-align: center;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.settingTable {
  text-anchor: top;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  color: rgb(0, 0, 0);
  background-color: rgb(255, 255, 255);
}

.settingTitle{
  color: #495AD1 !important;
  font-family: "Franklin Gothic Medium", "Tahoma";
  font-size: 1.25em;
  font-weight: bold;
  margin-bottom: 1px;
  margin-top: 20px;
}

